.settings-container {
  position: fixed;
  top: 0; left: 0; right: 0; 
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: black;
  z-index: 1;

  .page {
    display: flex;
    justify-content: center;
    align-items: center;
    
    background-color: rgb(255, 255, 255);
    width: 700px;
    height: 500px;
    border-radius: 15px;
  }

  display: none;
}